<template>
  <div class="wrap">
    <app-header title="报表下载" showBack="true"></app-header>
    <div class="main">
     <p>
        <mt-button size="small" type="primary" @click="clickStartTime">开始时间</mt-button>
        {{trimTime}}
     </p>
      <p>
        <mt-button size="small" type="primary" @click="clickEndTime">结束时间</mt-button>
        {{trimEndTime}}
      </p>
      <div v-show="dealerInfo.role === 3">
          <mt-radio
            title="分类下载"
            v-model="selectedlabel"
            :options="labels">
          </mt-radio>
      </div>
      <mt-button size="large"  type="primary" @click="download">下载</mt-button>
    </div>
    <mt-datetime-picker
      ref="startTime"
      v-model="startTime"
      type="date">
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="endTime"
      v-model="endTime"
      type="date">
    </mt-datetime-picker>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import {getQueryString} from '../../lib/util'
import {getCouponLabels} from '../../api/maintain'
export default {
  name: 'Dealer-Item',
  data () {
    return {
      showDate: false,
      startTime: '',
      endTime: '',
      dealerInfo: '',
      labels: [],
      selectedlabel: ''
    }
  },
  created () {
    this.getLabel()
    if (typeof this.$store.getters.getDealerInfo === 'string') {
      this.dealerInfo = JSON.parse(this.$store.getters.getDealerInfo)
    } else {
      this.dealerInfo = this.$store.getters.getDealerInfo
    }
  },
  computed: {
    trimTime: function () {
      let date = new Date(this.startTime)
      let _y = date.getFullYear()
      let _m = date.getMonth() + 1
      let _d = date.getDate()
      return `${_y} - ${_m} - ${_d}`
    },
    trimEndTime: function () {
      let date = new Date(this.endTime)
      let _y = date.getFullYear()
      let _m = date.getMonth() + 1
      let _d = date.getDate()
      return `${_y} - ${_m} - ${_d}`
    }
  },
  methods: {
    clickStartTime () {
      this.$refs.startTime.open()
    },
    clickEndTime () {
      this.$refs.endTime.open()
    },
    getLabel () {
      getCouponLabels()
        .then(data => {
          if (data.state) {
            let datas = data.data.rank
            for (let i in datas) {
              if (datas[i].labels) {
                for (let j in datas[i].labels) {
                  let _o = {
                    label: datas[i].labels[j].label_cn,
                    value: datas[i].labels[j].label_id
                  }
                  this.labels.push(_o)
                }
              }
            }
          }
        })
    },
    download () {
      if (Number(getQueryString('type')) === 1) {
        let _start = new Date(this.startTime).getTime() / 1000
        let _end = new Date(this.endTime).getTime() / 1000
        window.open('http://api.hongyanche.com/maintain_admin/coupon_send_download?start=' + _start + '&end=' + _end)
      } else {
        let _start = new Date(this.startTime).getTime() / 1000
        let _end = new Date(this.endTime).getTime() / 1000
        window.open('http://api.hongyanche.com/maintain_admin/download?start=' + _start + '&end=' + _end + '&label_id=' + this.selectedlabel)
      }
    }
  },
  components: {AppHeader}
}
</script>

<style lang="less" scoped>
.wrap {
  height: 100%;
  padding: 55px 0;
  padding-bottom: 300px;
  background: #fff;
  .main {
    margin-bottom: 66px;
    p {
      padding: 0 1rem;
    }
  }
}
</style>



